import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';

import './index.scss';
import Avatar from '../../ui/Avatar';
import Label, { StringSet } from '../../ui/Label';
import { Typography, Colors } from '../../ui/Label/types';
import Modal from '../../ui/Modal';
import { Type as ButtonTypes } from '../../ui/Button/type';
import Checkbox from '../../ui/Checkbox';

const InviteMembers = (props) => {
  const {
    userId,
    userQueryCreator,
    closeModal,
    onSubmit,
    submitText,
    titleText,
  } = props;

  const [users, setUsers] = useState([]);
  const [selectedUsers, setSelectedUsers] = useState({});
  const [usersDataSource, setUsersDataSource] = useState({});

  const selectedCount = Object.keys(selectedUsers).length;

  useEffect(() => {
    const applicationUserListQuery = userQueryCreator();
    setUsersDataSource(applicationUserListQuery);
    applicationUserListQuery.next((users_, error) => {
      if (error) {
        return;
      }
      setUsers(users_);
    });
  }, []);

  return (
    <Modal
      onCancel={closeModal}
      onSubmit={() => {
        onSubmit(Object.keys(selectedUsers));
        closeModal();
      }}
      submitText={submitText}
      titleText={titleText}
      type={ButtonTypes.PRIMARY}
    >
      <div>
        <Label
          color={(selectedCount > 0) ? Colors.PRIMARY : Colors.ONBACKGROUND_3}
          type={Typography.CAPTION_1}
        >
          {`${selectedCount} ${StringSet.MODAL__INVITE_MEMBER__SELECTEC}`}
        </Label>
        <div
          className="sendbird-create-channel--scroll"
          onScroll={(e) => {
            const { hasNext } = usersDataSource;
            const fetchMore = (
              e.target.clientHeight + e.target.scrollTop === e.target.scrollHeight
            );

            if (hasNext && fetchMore) {
              usersDataSource.next((usersBatch, error) => {
                if (error) {
                  return;
                }
                setUsers([
                  ...users,
                  ...usersBatch,
                ]);
              });
            }
          }}
        >
          {
            users.map((user) => (user.userId !== userId) && (
              <div key={user.userId} className="sendbird-create-channel--user-row">
                <div className="sendbird-create-channel--user-row--avatar">
                  <Avatar height="40px" width="40px" src={user.profileUrl} />
                </div>
                <div className="sendbird-create-channel--user-row--label">
                  <Label type={Typography.SUBTITLE_1} color={Colors.ONBACKGROUND_1}>
                    {user.nickname}
                  </Label>
                </div>
                {/* eslint-disable-next-line jsx-a11y/label-has-associated-control */}
                <label htmlFor={user.userId} className="sendbird-create-channel--user-row--checkbox">
                  <Checkbox
                    id={user.userId}
                    checked={selectedUsers[user.userId]}
                    onChange={
                      (event) => {
                        const modifiedSelectedUsers = {
                          ...selectedUsers,
                          [event.target.id]: event.target.checked,
                        };

                        if (!event.target.checked) {
                          delete modifiedSelectedUsers[event.target.id];
                        }

                        setSelectedUsers(modifiedSelectedUsers);
                      }
                    }
                  />
                </label>
              </div>
            ))
          }
        </div>
      </div>
    </Modal>
  );
};

InviteMembers.propTypes = {
  userId: PropTypes.string.isRequired,
  userQueryCreator: PropTypes.func.isRequired,
  closeModal: PropTypes.func.isRequired,
  onSubmit: PropTypes.func.isRequired,
  submitText: PropTypes.string,
  titleText: PropTypes.string,
};
InviteMembers.defaultProps = {
  submitText: 'create',
  titleText: 'Create new channel',
};

export default InviteMembers;
