import React from 'react';
import PropTypes from 'prop-types';
import { createPortal } from 'react-dom';

import './index.scss';

import { MODAL_ROOT } from '../../hooks/useModal/ModalRoot';
import Icon, { Types as IconTypes } from '../Icon';
import Button from '../Button';
import { Type as ButtonTypes } from '../Button/type';
import Label, { StringSet } from '../Label';
import { Typography, Colors } from '../Label/types';

export const ModalHeader = ({ titleText }) => (
  <div className="sendbird-modal-header">
    <Label type={Typography.H_1} color={Colors.ONBACKGROUND_1}>
      {titleText}
    </Label>
  </div>
);
ModalHeader.propTypes = {
  titleText: PropTypes.string.isRequired,
};

export const ModalBody = ({ children }) => (
  <div className="sendbird-modal-body">{children}</div>
);
ModalBody.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.element.isRequired,
    PropTypes.arrayOf(PropTypes.element.isRequired),
  ]).isRequired,
};

export const ModalFooter = ({
  onSubmit,
  onCancel,
  submitText,
  type,
}) => (
  <div className="sendbird-modal-footer">
    <Button type={ButtonTypes.SECONDARY} onClick={onCancel}>
      <Label type={Typography.BUTTON_1} color={Colors.ONBACKGROUND_1}>
        {StringSet.BUTTON__CANCEL}
      </Label>
    </Button>
    <Button type={type} onClick={onSubmit}>
      {submitText}
    </Button>
  </div>
);
ModalFooter.propTypes = {
  onCancel: PropTypes.func.isRequired,
  onSubmit: PropTypes.func.isRequired,
  submitText: PropTypes.string.isRequired,
  type: PropTypes.string,
};
ModalFooter.defaultProps = {
  type: ButtonTypes.DANGER,
};

function Modal(props) {
  const {
    children,
    onCancel,
    onSubmit,
    submitText,
    titleText,
    hideFooter,
    type,
  } = props;
  return createPortal((
    <div className="sendbird-modal">
      <div className="sendbird-modal-content">
        <ModalHeader titleText={titleText} />
        <ModalBody>{children}</ModalBody>
        {
          !hideFooter && (
            <ModalFooter
              onCancel={onCancel}
              onSubmit={onSubmit}
              submitText={submitText}
              type={type}
            />
          )
        }
        <div className="sendbird-modal-close">
          <Icon type={IconTypes.CLOSE} onClick={onCancel} />
        </div>
      </div>
      <div className="sendbird-modal-backdrop" />
    </div>
  ), document.getElementById(MODAL_ROOT));
}

Modal.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.element.isRequired,
    PropTypes.arrayOf(PropTypes.element.isRequired),
  ]),
  onCancel: PropTypes.func.isRequired,
  onSubmit: PropTypes.func.isRequired,
  hideFooter: PropTypes.bool,
  type: PropTypes.string,
};
Modal.defaultProps = {
  children: (<></>),
  hideFooter: false,
  type: ButtonTypes.DANGER,
};

export default Modal;
