import React from 'react';
import PropTypes from 'prop-types';

import './index.scss';
import { OutgoingUserMessage, IncomingUserMessage } from '../UserMessage';

const noop = () => {};

export default function Message(props) {
  const {
    isByMe,
    message,
    className,
    showEdit,
    showRemove,
    status,
  } = props;
  const injectingClassName = (Array.isArray(className)) ? className : [className];
  injectingClassName.push(`sendbird-message${isByMe ? '--outgoing' : '--incoming'}`);

  if (!message) return null;

  return (
    <div className={[...injectingClassName, 'sendbird-message'].join(' ')}>
      {
        isByMe
          ? (
            <OutgoingUserMessage
              message={message}
              showEdit={showEdit}
              showRemove={showRemove}
              status={status}
            />
          )
          : <IncomingUserMessage message={message} />
        // file messages should be here
      }
    </div>
  );
}

Message.propTypes = {
  isByMe: PropTypes.bool,
  message: PropTypes.objectOf(PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
    PropTypes.bool,
    PropTypes.array,
    PropTypes.object,
  ])).isRequired,
  className: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.arrayOf(PropTypes.string),
  ]),
  showEdit: PropTypes.func,
  status: PropTypes.string.isRequired,
  showRemove: PropTypes.func,
};

Message.defaultProps = {
  isByMe: false,
  className: '',
  showEdit: noop,
  showRemove: noop,
};
