import React from 'react';
import PropTypes from 'prop-types';

import './index.scss';
import { Typography, Colors } from './types';
import { changeTypographyToClassName, changeColorToClassName } from './utils';
import getStringSet from './stringSet';

const StringSet = getStringSet('en');

export { Typography, Colors, StringSet };
export default function Label({
  type,
  color,
  children,
  className,
}) {
  const injectingClassName = (Array.isArray(className)) ? className : [className];
  return (
    <div
      className={
        [
          ...injectingClassName,
          'sendbird-label',
          changeTypographyToClassName(type),
          changeColorToClassName(color),
        ].join(' ')
      }
    >
      {children}
    </div>
  );
}

Label.propTypes = {
  type: PropTypes.oneOf(Object.keys(Typography)),
  color: PropTypes.oneOf(Object.keys(Colors)),
  children: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
    PropTypes.element,
  ]).isRequired,
  className: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.arrayOf(PropTypes.string),
  ]),
};

Label.defaultProps = {
  type: Typography.H_1,
  color: Colors.ONBACKGROUND_1,
  className: [],
};
