import React, { Component } from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';

import './conversation-scroll.scss';
import * as messageActionTypes from '../dux/actionTypes';

import MessageHOC from './MessageHOC';
import { getParsedStatus } from '../utils';

export default class ConversationScroll extends Component {
  onScroll = (e) => {
    const {
      scrollRef,
      hasMore,
      messagesDispatcher,
      messageSource,
    } = this.props;

    const element = e.target;
    const {
      scrollTop,
    } = element;
    if (scrollTop === 0) {
      if (!hasMore) {
        return;
      }
      const nodes = scrollRef.current.querySelectorAll('.sendbird-msg');
      const first = nodes[0];
      messageSource.load((messages, error) => {
        if (error) {
          return;
        }
        messagesDispatcher({
          type: messageActionTypes.GET_PREV_MESSAGES_SUCESS,
          payload: {
            messages,
            hasMore: messageSource.hasMore,
          },
        });
        setTimeout(() => {
          // https://github.com/scabbiaza/react-scroll-position-on-updating-dom
          first.scrollIntoView();
        });
      });
    }
  }

  render() {
    const {
      scrollRef,
      hasMore,
      initialized,
      allMessages,
      userId,
      deleteMessage,
      updateMessage,
      readStatus,
      currentGroupChannel,
      renderChatItem,
    } = this.props;
    return (
      <div className="sendbird-conversation__messages">
        <div
          ref={scrollRef}
          className="sendbird-conversation__scroll-container"
          onScroll={this.onScroll}
        >
          <div className="sendbird-conversation__padding">
            {hasMore && 'LoadMore'}
          </div>
          <div>
            {
              initialized
              && allMessages.map(
                (m, idx) => {
                  const prev = allMessages[idx - 1];
                  const prevCreatedAt = prev && prev.createdAt;
                  const currentCreatedAt = m.createdAt;
                  // https://stackoverflow.com/a/41855608
                  const hasSeperator = !(prevCreatedAt && (
                    moment(currentCreatedAt).isSame(moment(prevCreatedAt), 'day')
                  ));

                  if (renderChatItem) {
                    return (
                      <div key={m.messageId}>
                        {
                          renderChatItem({
                            message: m,
                            onDeleteMessage: deleteMessage,
                            onUpdateMessage: updateMessage,
                          })
                        }
                      </div>
                    );
                  }

                  return (
                    <MessageHOC
                      // show status for pending/failed messages
                      status={readStatus[m.messageId] || getParsedStatus(m, currentGroupChannel)}
                      key={m.messageId}
                      message={m}
                      userId={userId}
                      hasSeperator={hasSeperator}
                      deleteMessage={deleteMessage}
                      updateMessage={updateMessage}
                    />
                  );
                },
              )
            }
          </div>
        </div>
      </div>
    );
  }
}

ConversationScroll.propTypes = {
  // https://stackoverflow.com/a/52646941
  scrollRef: PropTypes.shape({ current: PropTypes.elementType }).isRequired,
  hasMore: PropTypes.bool,
  messagesDispatcher: PropTypes.func.isRequired,
  messageSource: PropTypes.shape({
    load: PropTypes.func,
    hasMore: PropTypes.bool,
  }).isRequired,
  initialized: PropTypes.bool,
  userId: PropTypes.string,
  allMessages: PropTypes.arrayOf(PropTypes.shape({
    createdAt: PropTypes.string,
  })).isRequired,
  deleteMessage: PropTypes.func.isRequired,
  updateMessage: PropTypes.func.isRequired,
  readStatus: PropTypes.shape({}).isRequired,
  currentGroupChannel: PropTypes.shape({}).isRequired,
  renderChatItem: PropTypes.element,
};

ConversationScroll.defaultProps = {
  hasMore: false,
  initialized: false,
  userId: '',
  renderChatItem: null,
};
