// SendBird disconnect. Invalidates currentUser
// eslint-disable-next-line max-len
export const getConnect = (store) => (userId, accessToken) => new Promise((resolve, reject) => {
  const { stores = {} } = store;
  const { sdkStore = {} } = stores;
  const { sdk } = sdkStore;
  if (!sdk) {
    reject(new Error('Sdk not found'));
  }
  if (!accessToken) {
    sdk.connect(userId, (response, error) => {
      if (error) {
        reject(error);
      } else {
        resolve(response);
      }
    });
  } else {
    sdk.connect(userId, accessToken, (response, error) => {
      if (error) {
        reject(error);
      } else {
        resolve(response);
      }
    });
  }
});

// SendBird disconnect. Invalidates currentUser
export const getDisconnect = (store) => () => new Promise((resolve, reject) => {
  const { stores = {} } = store;
  const { sdkStore = {} } = stores;
  const { sdk } = sdkStore;
  if (!sdk) {
    reject(new Error('Sdk not found'));
  }
  sdk.disconnect((response, error) => {
    if (error) {
      reject(error);
    } else {
      resolve(response);
    }
  });
});

// Using the updateCurrentUserInfo() method
// you can update a user's nickname and profile image with a URL
// eslint-disable-next-line max-len
export const getUpdateUserInfo = (store) => (nickName, profileUrl) => new Promise((resolve, reject) => {
  const { stores = {} } = store;
  const { sdkStore = {} } = stores;
  const { sdk } = sdkStore;
  if (!sdk) {
    reject(new Error('Sdk not found'));
  }
  sdk.updateCurrentUserInfo(nickName, profileUrl, (response, error) => {
    if (error) {
      reject(error);
    } else {
      resolve(response);
    }
  });
});

export default {
  getConnect,
  getDisconnect,
  getUpdateUserInfo,
};
