export const createChannel = (sdk, selectedUsers) => new Promise((resolve, reject) => {
  sdk.GroupChannel.createChannelWithUserIds(
    Object.keys(selectedUsers),
    false, // 'distinct' is false
    '', // NAME
    '', // COVER_IMAGE_OR_URL
    '', // DATA
    (groupChannel, error) => {
      if (error) {
        reject(error);
      }
      resolve(groupChannel);
    },
  );
});

export const getUsers = (sdk, setUsers, callback, setUsersDataSource, userListQuery) => {
  const applicationUserListQuery = (userListQuery && typeof userListQuery === 'function')
    ? userListQuery()
    : sdk.createApplicationUserListQuery();
  setUsersDataSource(applicationUserListQuery);
  applicationUserListQuery.next((users, error) => {
    if (error) {
      return;
    }
    setUsers(users);
    callback();
  });
};

export default createChannel;
