import Moment from 'moment';

export const getChannelAvatarSource = (channel) => {
  if (channel && channel.coverUrl) {
    return channel.coverUrl;
  }
  return channel && channel.members
    ? channel.members.map(({ profileUrl }) => profileUrl)
    : [];
};

export const getChannelTitle = (channel = {}) => {
  if (!channel || (!channel.name && !channel.members)) {
    return 'No Title';
  }
  if (channel.name) {
    return channel.name;
  }
  return channel.members
    .map(({ nickname }) => nickname)
    .join(', ');
};

export const prettyDate = (date) => (Moment(date, 'x').fromNow());

export const getLastSeenAt = (channel) => {
  if (!channel || !channel.getReadStatus) {
    return '';
  }
  const readStatus = channel.getReadStatus(true);
  const keysOfReadStatus = Object.keys(readStatus);
  return prettyDate(keysOfReadStatus.reduce((latest, key) => (
    latest > readStatus[key].last_seen_at ? latest : readStatus[key].last_seen_at
  ), 0));
};

export const getTotalMembers = (channel) => (
  channel && channel.memberCount
    ? channel.memberCount
    : 0
);

export const getLastMessage = (channel) => (
  channel && channel.lastMessage
    ? channel.lastMessage.message
    : ''
);

export const getChannelUnreadMessageCount = (channel) => (
  (channel && channel.unreadMessageCount)
    ? channel.unreadMessageCount
    : 0
);
