import React, { useRef } from 'react';
import PropTypes from 'prop-types';

import './index.scss';
import { getMessageCreatedAt, getSenderProfileUrl, getSenderName } from '../../utils/utils';
import Avatar from '../Avatar';
import Label, { Typography, Colors } from '../Label';
import Icon, { Types } from '../Icon';
import IconButton from '../IconButton';
import MessageStatus from '../MessageStatus';
import ContextMenu, { MenuItem, MenuItems } from '../ContextMenu';

import { copyToClipboard } from './util';

function OutgoingUserMessage({
  message,
  showEdit,
  showRemove,
  status,
}) {
  // TODO: when message.requestState is succeeded, consider if it's SENT or DELIVERED
  const parentRef = useRef(null);
  return (
    <div className="sendbird-user-message--outgoing">
      <ContextMenu
        menuTrigger={(toggleDropdown) => (
          <IconButton
            className="sendbird-user-message__more"
            width="32px"
            height="32px"
            onClick={toggleDropdown}
          >
            <Icon width="22px" height="22px" type={Types.MORE} />
          </IconButton>
        )}
        menuItems={(closeDropdown) => (
          <MenuItems parentRef={parentRef} closeDropdown={closeDropdown}>
            <MenuItem
              onClick={() => { copyToClipboard(message.message); closeDropdown(); }}
            >
              Copy
            </MenuItem>
            <MenuItem onClick={() => { showEdit(true); closeDropdown(); }}>
              Edit
            </MenuItem>
            <MenuItem onClick={() => { showRemove(true); closeDropdown(); }}>
              Delete
            </MenuItem>
          </MenuItems>
        )}
      />
      <div className="sendbird-user-message__tooltip" ref={parentRef}>
        <Label
          className="sendbird-user-message__tooltip__text"
          type={Typography.BODY_1}
          color={Colors.ONBACKGROUND_1}
        >
          {message.message}
        </Label>
      </div>
      <div className="sendbird-user-message__status">
        <MessageStatus message={message} status={status} />
      </div>
    </div>
  );
}

function IncomingUserMessage({
  message,
}) {
  const parentRef = useRef(null);
  return (
    <div className="sendbird-user-message--incoming">
      <Avatar
        className="sendbird-user-message__avatar"
        src={getSenderProfileUrl(message)}
        width="28px"
        height="28px"
      />
      <Label
        className="sendbird-user-message__sender-name"
        type={Typography.CAPTION_2}
        color={Colors.ONBACKGROUND_2}
      >
        {getSenderName(message)}
      </Label>
      <div className="sendbird-user-message__tooltip" ref={parentRef}>
        <Label
          className="sendbird-user-message__tooltip__text"
          type={Typography.BODY_1}
          color={Colors.ONBACKGROUND_1}
        >
          {message.message}
        </Label>
      </div>
      <Label
        className="sendbird-user-message__sent-at"
        type={Typography.CAPTION_3}
        color={Colors.ONBACKGROUND_2}
      >
        {getMessageCreatedAt(message)}
      </Label>
      <ContextMenu
        menuTrigger={(toggleDropdown) => (
          <IconButton
            className="sendbird-user-message__more"
            width="32px"
            height="32px"
            onClick={toggleDropdown}
          >
            <Icon width="22px" height="22px" type={Types.MORE} />
          </IconButton>
        )}
        menuItems={(closeDropdown) => (
          <MenuItems parentRef={parentRef} closeDropdown={closeDropdown}>
            <MenuItem
              onClick={() => { copyToClipboard(message.message); closeDropdown(); }}
            >
              Copy
            </MenuItem>
          </MenuItems>
        )}
      />
    </div>
  );
}


IncomingUserMessage.propTypes = {
  message: PropTypes.objectOf(PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
    PropTypes.bool,
    PropTypes.array,
    PropTypes.object,
  ])),
};

IncomingUserMessage.defaultProps = {
  message: {},
};

OutgoingUserMessage.propTypes = {
  message: PropTypes.objectOf(PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
    PropTypes.bool,
    PropTypes.array,
    PropTypes.object,
  ])),
  showEdit: PropTypes.func.isRequired,
  showRemove: PropTypes.func.isRequired,
  status: PropTypes.string.isRequired,
};

OutgoingUserMessage.defaultProps = {
  message: {},
};

export {
  OutgoingUserMessage,
  IncomingUserMessage,
};
