import React, { useState } from 'react';
import PropTypes from 'prop-types';

import './members-accordion.scss';

import Button from '../../../ui/Button';
import {
  Type as ButtonTypes,
  Size as ButtonSizes,
} from '../../../ui/Button/type';
import Avatar from '../../../ui/Avatar';
import Label, { StringSet } from '../../../ui/Label';
import { Typography, Colors } from '../../../ui/Label/types';
import Modal from '../../../ui/Modal';
import InviteMembers from '../../InviteMembers';

const MemebersAccordion = ({
  members,
  userQueryCreator,
  userId,
  onInviteMemebers,
}) => {
  const [showMoreModal, setShowMoreModal] = useState(false);
  const [showAddUserModal, setShowAddUserModal] = useState(false);
  return (
    <div className="sendbird-members-accordion">
      <div className="sendbird-members-accordion__list">
        {
          // display only first five members
          members.slice(0, 6).map((member) => (
            <div className="sendbird-members-accordion__member" key={member.userId}>
              <div className="sendbird-members-accordion__member-avatar">
                <Avatar height="24px" width="24px" />
              </div>
              <Label type={Typography.SUBTITLE_2} color={Colors.ONBACKGROUND_1}>
                {member.nickname}
              </Label>
            </div>
          ))
        }
      </div>
      <div className="sendbird-members-accordion__footer">
        <Button
          type={ButtonTypes.SECONDARY}
          size={ButtonSizes.SMALL}
          onClick={() => setShowMoreModal(true)}
        >
          <Label type={Typography.BUTTON_1} color={Colors.ONBACKGROUND_1}>
            {StringSet.CHANNEL_SETTING__MEMBERS__SEE_ALL_MEMBERS}
          </Label>
        </Button>
        {
          showMoreModal && (
            <Modal
              hideFooter
              onCancel={() => setShowMoreModal(false)}
              onSubmit={() => { }}
              titleText={`${members.length} ${StringSet.MODAL__USER_LIST__TITLE}`}
            >
              <div className="sendbird-more-members__popup-scroll">
                {
                  members.map((m) => (
                    <div key={m.userId} className="sendbird-more-members__popup-member">
                      <div className="sendbird-more-members__popup-avatar">
                        <Avatar src={m.profileUrl} height="40px" width="40px" />
                      </div>
                      <Label type={Typography.SUBTITLE_1} Colors={Colors.ONBACKGROUND_1}>
                        {m.nickname}
                      </Label>
                    </div>
                  ))
                }
              </div>
            </Modal>
          )
        }
        <Button
          type={ButtonTypes.SECONDARY}
          size={ButtonSizes.SMALL}
          onClick={() => setShowAddUserModal(true)}
        >
          <Label type={Typography.BUTTON_1} color={Colors.ONBACKGROUND_1}>
            {StringSet.CHANNEL_SETTING__MEMBERS__INVITE_MEMBER}
          </Label>
        </Button>
        {
          showAddUserModal && (
            <InviteMembers
              titleText={StringSet.MODAL__INVITE_MEMBER__TITLE}
              submitText={StringSet.BUTTON__OK}
              closeModal={() => setShowAddUserModal(false)}
              userId={userId}
              userQueryCreator={userQueryCreator}
              onSubmit={onInviteMemebers}
            />
          )
        }
      </div>
    </div>
  );
};

MemebersAccordion.propTypes = {
  members: PropTypes.arrayOf(PropTypes.shape({})),
  userQueryCreator: PropTypes.func.isRequired,
  onInviteMemebers: PropTypes.func.isRequired,
  userId: PropTypes.string.isRequired,
};

MemebersAccordion.defaultProps = {
  members: [],
};

export default MemebersAccordion;
