import React from 'react';
import PropTypes from 'prop-types';

import './index.scss';
import Icon, { Types } from '../Icon';
import Label, { Typography, StringSet } from '../Label';
import Loader from '../Loader';
import MessageStatusType from './type';

import { Colors } from '../../utils/color';
import { getMessageCreatedAt } from '../../utils/utils';

export default function MessageStatus({
  message,
  status,
  className,
  onClickEvents,
}) {
  const injectingClassName = (Array.isArray(className)) ? className : [className];

  const label = () => {
    switch (status) {
      case MessageStatusType.PENDING: {
        return null;
      }
      case MessageStatusType.SENT:
      case MessageStatusType.DELIVERED:
      case MessageStatusType.READ: {
        return (
          <Label
            className="sendbird-message-status__text"
            type={Typography.CAPTION_3}
            color={Colors.ONBACKGROUND_2}
          >
            {getMessageCreatedAt(message)}
          </Label>
        );
      }
      case MessageStatusType.FAILED: {
        // check onClickEvents
        return (
          <Label
            className="sendbird-message-status__text"
            type={Typography.CAPTION_3}
            color={Colors.ERROR}
          >
            {StringSet.MESSAGE_STATUS__SENDING_FAILED}
            <span
              className="sendbird-message-status__text__try-again"
              role="button"
              tabIndex={0}
              onClick={onClickEvents[0]}
              onKeyPress={onClickEvents[0]}
            >
              {StringSet.MESSAGE_STATUS__TRY_AGAIN}
            </span>
            {StringSet.MESSAGE_STATUS__OR}
            <span
              className="sendbird-message-status__text__delete"
              role="button"
              tabIndex={0}
              onClick={onClickEvents[1]}
              onKeyPress={onClickEvents[1]}
            >
              {StringSet.MESSAGE_STATUS__DELETE}
            </span>
          </Label>
        );
      }
      default: return null;
    }
  };

  const icon = {
    PENDING: (
      <Loader className="sendbird-message-status__icon" width="16px" height="16px">
        <Icon type={Types.SPINNER} width="16px" height="16px" />
      </Loader>
    ),
    SENT: <Icon className="sendbird-message-status__icon" width="16px" height="16px" type={Types.SENT} />,
    DELIVERED: <Icon className="sendbird-message-status__icon" width="16px" height="16px" type={Types.DELIVERED} />,
    READ: <Icon className="sendbird-message-status__icon" width="16px" height="16px" type={Types.READ} />,
    FAILED: <Icon className="sendbird-message-status__icon" width="16px" height="16px" type={Types.ERROR_FILLED} />,
  };

  return (
    <div className={[...injectingClassName, 'sendbird-message-status'].join(' ')}>
      {label(status)}
      {icon[status]}
    </div>
  );
}

MessageStatus.propTypes = {
  message: PropTypes.objectOf(PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
    PropTypes.bool,
    PropTypes.array,
    PropTypes.object,
  ])),
  status: PropTypes.string,
  className: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.arrayOf(PropTypes.string),
  ]),
  onClickEvents: PropTypes.arrayOf(PropTypes.func),
};

MessageStatus.defaultProps = {
  message: null,
  status: '',
  className: '',
  onClickEvents: [() => { }, () => { }],
};
