import React, { useState, useRef } from 'react';
import PropTypes from 'prop-types';

import './index.scss';

import IconButton from '../IconButton';
import Button from '../Button';
import { Type, Size } from '../Button/type';

import Icon, { Types as IconTypes } from '../Icon';
import Label, { Typography, Colors, StringSet } from '../Label';

// import IconSend from '../../svgs/icon-send.svg';
// import IconAttach from '../../svgs/icon-attach.svg';

const LINE_HEIGHT = 76;
const noop = () => { };

// https://stackoverflow.com/questions/17772260/textarea-auto-height
const setHeight = (e) => {
  const elem = e.target;
  elem.style.height = 'auto';
  if (elem.scrollHeight > LINE_HEIGHT) {
    elem.style.height = `${elem.scrollHeight}px`;
  } else {
    elem.style.height = '';
  }
};

const handleUploadFile = (callback) => (event) => {
  if (event.target.files && event.target.files[0]) {
    callback(event.target.files[0]);
  }
};

const MessageInput = React.forwardRef((props, ref) => {
  const {
    isEdit,
    disabled,
    value,
    name,
    placeholder,
    maxLength,
    onFileUpload,
    onSendMessage,
    onCancelEdit,
    onStartTyping,
  } = props;

  const fileInputRef = useRef(null);
  const [inputValue, setInputValue] = useState(value);

  return (
    <form
      className={`${isEdit ? 'sendbird-message-input__edit' : ''}`}
    >
      <div
        className={`
          sendbird-message-input
          ${disabled ? 'sendbird-message-input__disabled' : ''}
        `}
      >
        <textarea
          disabled={disabled}
          ref={ref}
          name={name}
          value={inputValue}
          className="sendbird-message-input--textarea"
          maxLength={maxLength}
          onKeyDown={() => {
            onStartTyping();
          }}
          onChange={(e) => {
            setInputValue(e.target.value);
          }}
          onInput={setHeight}
        />
        {/* placeholder */}
        {!inputValue && (
          <Label
            type={Typography.BODY_1}
            color={Colors.ONBACKGROUND_3}
            className="sendbird-message-input--placeholder"
          >
            {placeholder || StringSet.CHANNEL__MESSAGE_INPUT__PLACE_HOLDER}
          </Label>
        )}
        {/* upload icon */}
        {
          (!isEdit && !inputValue) && (
            <IconButton
              className="sendbird-message-input--attach"
              height="32px"
              width="32px"
              onClick={() => {
                // todo: clear previous input
                fileInputRef.current.click();
              }}
            >
              <Icon type={IconTypes.ATTACH} width="20px" height="20px" />
              <input
                type="file"
                ref={fileInputRef}
                onChange={handleUploadFile(onFileUpload)}
                className="sendbird-message-input--attach-input"
              />
            </IconButton>
          )
        }
        {/* send icon */}
        {
          (!isEdit && inputValue) && (
            <IconButton
              className="sendbird-message-input--send"
              height="32px"
              width="32px"
              onClick={() => {
                onSendMessage(inputValue);
                setInputValue('');
              }}
            >
              <Icon type={IconTypes.SEND} width="20px" height="20px" />
            </IconButton>
          )
        }
      </div>
      {/* Edit */}
      {
        isEdit && (
          <div className="sendbird-message-input--edit-action">
            <Button
              className="sendbird-message-input--edit-action__cancel"
              type={Type.SECONDARY}
              size={Size.SMALL}
              onClick={onCancelEdit}
            >
              {StringSet.BUTTON__CANCEL}
            </Button>
            <Button
              className="sendbird-message-input--edit-action__save"
              type={Type.PRIMARY}
              size={Size.SMALL}
              onClick={() => {
                onSendMessage(name, inputValue, () => {
                  onCancelEdit();
                });
              }}
            >
              {StringSet.BUTTON__SAVE}
            </Button>
          </div>
        )
      }
    </form>
  );
});

MessageInput.propTypes = {
  placeholder: PropTypes.string,
  isEdit: PropTypes.bool,
  name: PropTypes.string,
  value: PropTypes.string,
  disabled: PropTypes.bool,
  maxLength: PropTypes.number,
  onFileUpload: PropTypes.func,
  onSendMessage: PropTypes.func.isRequired,
  onStartTyping: PropTypes.func,
  onCancelEdit: PropTypes.func,
};

MessageInput.defaultProps = {
  value: '',
  name: 'sendbird-message-input',
  isEdit: false,
  disabled: false,
  placeholder: '',
  maxLength: 5000,
  onFileUpload: noop,
  onCancelEdit: noop,
  onStartTyping: noop,
};

export default MessageInput;
