import React, { useState } from 'react';
import PropTypes from 'prop-types';

import './add-channel.scss';

import useModal, { ModalComponents } from '../../../hooks/useModal';
import IconButton from '../../../ui/IconButton';
import Avatar from '../../../ui/Avatar';

import Button from '../../../ui/Button';
import { Type } from '../../../ui/Button/type';

import Label, { StringSet } from '../../../ui/Label';
import { Typography, Colors } from '../../../ui/Label/types';

import Icon, { Types } from '../../../ui/Icon';

import { createChannel, getUsers } from './utils';
import { CREATE_CHANNEL } from '../dux/actionTypes';
import Checkbox from '../../../ui/Checkbox';

export default function AddChannel({
  sdk,
  channelListDispatcher,
  userId,
  userListQuery,
}) {
  const [Modal, openModal, closeModal] = useModal('sendbird-create-channel');
  const [users, setUsers] = useState([]);
  const [selectedUsers, setSelectedUsers] = useState({});
  const [usersDataSource, setUsersDataSource] = useState({});
  const selectedCount = Object.keys(selectedUsers).length;

  return (
    <IconButton height="32px" width="32px">
      <Icon
        type={Types.CREATE}
        width="22px"
        height="22px"
        onClick={() => {
          // reset selectedusers
          setSelectedUsers({});
          // todo - add loader(?)
          getUsers(
            sdk,
            setUsers,
            openModal,
            setUsersDataSource,
            userListQuery,
          );
        }}
      />
      <Modal>
        <div className="sendbird-create-channel--content">
          <ModalComponents.ModalHeader>
            <Label type={Typography.H_1} color={Colors.ONBACKGROUND_1}>
              {StringSet.MODAL__CREATE_CHANNEL__TITLE}
            </Label>
            <ModalComponents.ModalClose>
              <IconButton onClick={closeModal} height="32px" width="32px">
                <Icon type={Types.CLOSE} width="22px" height="22px" />
              </IconButton>
            </ModalComponents.ModalClose>
          </ModalComponents.ModalHeader>
          <Label
            color={(selectedCount > 0) ? Colors.PRIMARY : Colors.ONBACKGROUND_3}
            type={Typography.CAPTION_1}
          >
            {`${selectedCount} ${StringSet.MODAL__CREATE_CHANNEL__SELECTED}`}
          </Label>
          <div
            className="sendbird-create-channel--scroll"
            onScroll={(e) => {
              const { hasNext } = usersDataSource;
              const fetchMore = (
                e.target.clientHeight + e.target.scrollTop === e.target.scrollHeight
              );

              if (hasNext && fetchMore) {
                usersDataSource.next((usersBatch, error) => {
                  if (error) {
                    return;
                  }
                  setUsers([
                    ...users,
                    ...usersBatch,
                  ]);
                });
              }
            }}
          >
            {
              users.map((user) => (user.userId !== userId) && (
                <div key={user.userId} className="sendbird-create-channel--user-row">
                  <div className="sendbird-create-channel--user-row--avatar">
                    <Avatar height="40px" width="40px" src={user.profileUrl} />
                  </div>
                  <div className="sendbird-create-channel--user-row--label">
                    <Label type={Typography.SUBTITLE_1} color={Colors.ONBACKGROUND_1}>
                      {user.nickname}
                    </Label>
                  </div>
                  {/* eslint-disable-next-line jsx-a11y/label-has-associated-control */}
                  <label htmlFor={user.userId} className="sendbird-create-channel--user-row--checkbox">
                    <Checkbox
                      role="checkbox"
                      aria-labelledby={user.userId}
                      id={user.userId}
                      checked={selectedUsers[user.userId]}
                      onChange={
                        (event) => {
                          const modifiedSelectedUsers = {
                            ...selectedUsers,
                            [event.target.id]: event.target.checked,
                          };

                          if (!event.target.checked) {
                            delete modifiedSelectedUsers[event.target.id];
                          }

                          setSelectedUsers(modifiedSelectedUsers);
                        }
                      }
                    />
                  </label>
                </div>
              ))
            }
          </div>
          <ModalComponents.ModalFooter>
            <Button
              className="sendbird-create-channel__footer__close"
              type={Type.SECONDARY}
              onClick={closeModal}
            >
              {StringSet.BUTTON__CLOSE}
            </Button>
            <Button
              className="sendbird-create-channel__footer__ok"
              type={Type.PRIMARY}
              disabled={Object.keys(selectedUsers).length < 1}
              onClick={
                () => createChannel(sdk, selectedUsers)
                  .then((channel) => {
                    // maybe - do this in event listener
                    channelListDispatcher({
                      type: CREATE_CHANNEL,
                      payload: channel,
                    });
                    closeModal();
                  })
                // show error toast?
                // .catch((e) => {
                //   console.warn(e);
                // })
              }
            >
              {StringSet.BUTTON__OK}
            </Button>
          </ModalComponents.ModalFooter>
        </div>
      </Modal>
    </IconButton>
  );
}

AddChannel.propTypes = {
  sdk: PropTypes.shape({}).isRequired,
  channelListDispatcher: PropTypes.func.isRequired,
  userId: PropTypes.string.isRequired,
  userListQuery: PropTypes.func.isRequired,
};
