import React, { useRef, useState } from 'react';
import PropTypes from 'prop-types';

import './index.scss';

import Avatar from '../Avatar';
import Label, { Typography, Colors } from '../Label';
import ContextMenu, { MenuItem, MenuItems } from '../ContextMenu';
import IconButton from '../IconButton';
import Icon, { Types } from '../Icon';
import MessageStatus from '../MessageStatus';
import {
  isImage,
  isVideo,
  unSupported,
} from '../FileViewer/types';
import {
  getSenderName,
  getSenderProfileUrl,
} from '../../utils/utils';

const noop = () => { };

export default function ThumbnailMessage({
  message = {},
  isByMe,
  onClick,
  showRemove,
  status,
}) {
  const {
    type,
    url,
    name = '',
  } = message;
  const parentRef = useRef(null);
  const [imgLoaded, setImgLoaded] = useState(false);
  return (
    <div
      className={[
        'sendbird-thumbnail',
        (!isByMe ? 'sendbird-thumbnail--incoming' : ''),
        (!imgLoaded ? 'sendbird-thumbnail--loading' : ''),
      ].join(' ')}
    >
      {
        !isByMe && (
          <>
            <Avatar
              className="sendbird-thumbnail__avatar"
              src={getSenderProfileUrl(message)}
              width="28px"
              height="28px"
            />
            <Label
              className="sendbird-thumbnail__sender-name"
              type={Typography.CAPTION_2}
              color={Colors.ONBACKGROUND_2}
            >
              {getSenderName(message) || ''}
            </Label>
          </>
        )
      }
      <div className="sendbird-thumbnail__body">
        {
          isByMe && (
            <ContextMenu
              menuTrigger={(toggleDropdown) => (
                <IconButton
                  className="sendbird-thumbnail__more"
                  width="32px"
                  height="32px"
                  onClick={toggleDropdown}
                >
                  <Icon width="22px" height="22px" type={Types.MORE} />
                </IconButton>
              )}
              menuItems={(closeDropdown) => (
                <MenuItems parentRef={parentRef} closeDropdown={closeDropdown}>
                  <MenuItem onClick={() => { showRemove(true); closeDropdown(); }}>
                    Delete
                  </MenuItem>
                </MenuItems>
              )}
            />
          )
        }
        <div
          ref={parentRef}
          onClick={() => onClick(true)}
          onKeyDown={() => onClick(true)}
          tabIndex={0}
          role="button"
          className="sendbird-thumbnail__wrap"
        >
          {
            isVideo(type) && (
              // eslint-disable-next-line jsx-a11y/media-has-caption
              <video
                className="sendbird-thumbnail__video"
                onLoadedData={() => setImgLoaded(true)}
              >
                <source src={url} type={type} />
              </video>
            )
          }
          {
            isImage(type) && (
              <img
                onLoad={() => setImgLoaded(true)}
                src={url}
                alt={name}
                className="sendbird-thumbnail__img"
              />
            )
          }
          {
            unSupported(type) && (
              <div className="sendbird-thumbnail__other">
                Unknown type
              </div>
            )
          }
          <div className="sendbird-thumbnail__wrap-overlay" />
        </div>
        {isByMe && (
          <div className="sendbird-thumbnail__status">
            <MessageStatus message={message} status={status} />
          </div>
        )}
      </div>
    </div>
  );
}

ThumbnailMessage.propTypes = {
  message: PropTypes.shape({
    type: PropTypes.string,
    url: PropTypes.string,
    name: PropTypes.string,
  }).isRequired,
  status: PropTypes.string,
  isByMe: PropTypes.bool,
  onClick: PropTypes.func.isRequired,
  showRemove: PropTypes.func,
};

ThumbnailMessage.defaultProps = {
  isByMe: false,
  showRemove: noop,
  status: '',
};
