import React, { useState, useRef } from 'react';
import PropTypes from 'prop-types';

import ContextMenu, { MenuItem, MenuItems } from '../../../ui/ContextMenu';

import IconButton from '../../../ui/IconButton';
import Icon, { Types } from '../../../ui/Icon';
import { StringSet } from '../../../ui/Label';
import LeaveChannelModal from '../../ChannelSettings/components/LeaveChannel';

export default function ChannelPreviewAction({ onLeaveChannel }) {
  const parentRef = useRef(null);
  const [showModal, setShowModal] = useState(false);

  return (
    <div
      role="button"
      style={{ display: 'inline-block' }}
      onKeyDown={(e) => { e.stopPropagation(); }}
      tabIndex={0}
      onClick={(e) => { e.stopPropagation(); }}
    >
      <ContextMenu
        menuTrigger={(toggleDropdown) => (
          <IconButton
            ref={parentRef}
            onClick={toggleDropdown}
            height="32px"
            width="32px"
          >
            <Icon type={Types.MORE} width="22px" height="22px" />
          </IconButton>
        )}
        menuItems={(closeDropdown) => (
          <MenuItems parentRef={parentRef} closeDropdown={closeDropdown}>
            <MenuItem
              onClick={() => {
                setShowModal(true);
                closeDropdown();
              }}
            >
              {StringSet.CHANNEL_SETTING__LEAVE_CHANNEL__TITLE}
            </MenuItem>
          </MenuItems>
        )}
      />
      {
        showModal && (
          <LeaveChannelModal
            onCloseModal={() => setShowModal(false)}
            onLeaveChannel={onLeaveChannel}
          />
        )
      }
    </div>
  );
}

ChannelPreviewAction.propTypes = {
  onLeaveChannel: PropTypes.func.isRequired,
};
