import Sb from 'sendbird';

import { INIT_SDK, SET_SDK_LOADING } from './actionTypes';
import { INIT_USER, UPDATE_USER_INFO } from '../user/actionTypes';

export const disconnectSDK = () => {
  // cleanup
};

export const connectToSDK = ({
  userId,
  appId,
  sdkStore,
  nickname,
}, dispatchers) => {
  const {
    sdkDispatcher,
    userDispatcher,
  } = dispatchers;

  sdkDispatcher({ type: SET_SDK_LOADING, payload: true });
  if (sdkStore && sdkStore.disconnect) {
    disconnectSDK();
  }
  // to do: accessToken
  if (userId && appId) {
    const sdk = new Sb({ appId });
    sdk.connect(userId, (user, error) => {
      if (!error) {
        sdkDispatcher({ type: INIT_SDK, payload: sdk });
        userDispatcher({ type: INIT_USER, paylod: user });
        if (nickname) {
          sdk.updateCurrentUserInfo(nickname, user.profileUrl, (namedUser, err) => {
            if (!err) {
              userDispatcher({ type: UPDATE_USER_INFO, payload: namedUser });
            }
          });
        }
      } else {
        // to do: error handling
        // eslint-disable-next-line no-console
        console.warn('Error', { error });
      }
    });
  }
};
