import * as actions from './actionTypes';

export default function reducer(state, action) {
  switch (action.type) {
    case actions.INIT_USER:
      return {
        initialized: true,
        loading: false,
        user: action.payload,
      };
    case actions.UPDATE_USER_INFO:
      return {
        ...state,
        user: action.payload,
      };
    default:
      return state;
  }
}
