import * as sdkActions from './actionTypes';

export default function reducer(state, action) {
  switch (action.type) {
    case sdkActions.SET_SDK_LOADING:
      return {
        ...state,
        loading: action.payload,
      };
    case sdkActions.INIT_SDK:
      return {
        sdk: action.payload,
        initialized: true,
        loading: false,
      };
    default:
      return state;
  }
}
