import * as actionTypes from './actionTypes';
import initialState from './initialState';

export default function reducer(state, action) {
  switch (action.type) {
    case actionTypes.RESET_MESSAGES:
      return initialState;
    case actionTypes.GET_PREV_MESSAGES_START:
      return {
        ...state,
        loading: true,
      };
    case actionTypes.GET_PREV_MESSAGES_SUCESS:
      return {
        ...state,
        loading: false,
        initialized: true,
        hasMore: action.payload.hasMore,
        allMessages: [
          // todo: how to add date seperator?ß
          ...action.payload.messages,
          ...state.allMessages,
        ],
      };
    case actionTypes.SEND_MESSAGEGE_START:
      return {
        ...state,
        allMessages: [
          ...state.allMessages,
          { ...action.payload },
        ],
      };
    case actionTypes.SEND_MESSAGEGE_SUCESS:
      return {
        ...state,
        allMessages: state.allMessages.map((m) => (
          m.reqId === action.payload.reqId
            ? action.payload
            : m
        )),
      };
    // todo: implement complex cases
    case actionTypes.SEND_MESSAGEGE_FAILURE:
      return {
        ...state,
        allMessages: state.allMessages.map((m) => (
          m.reqId === action.payload.reqId
            ? { ...action.payload, failed: true }
            : m
        )),
      };
    case actionTypes.SET_CURRENT_CHANNEL: {
      return {
        ...state,
        currentChannel: action.payload,
      };
    }
    case actionTypes.ON_MESSAGE_RECEIVED: {
      const { channel, message } = action.payload;
      const { currentChannel } = state;
      if (channel.url !== currentChannel) {
        return state;
      }
      return {
        ...state,
        allMessages: [...state.allMessages, message],
      };
    }
    case actionTypes.ON_MESSAGE_UPDATED:
      return {
        ...state,
        allMessages: state.allMessages.map((m) => (
          m.messageId === action.payload.messageId
            ? action.payload
            : m
        )),
      };
    case actionTypes.ON_MESSAGE_DELETED:
      return {
        ...state,
        allMessages: state.allMessages.filter((m) => (
          m.messageId !== action.payload
        )),
      };
    default:
      return state;
  }
}
