import React, { useState } from 'react';
import PropTypes from 'prop-types';

import './channel-profile.scss';

import Avatar from '../../../ui/Avatar';
import Label, { Typography, StringSet } from '../../../ui/Label';
import EditDetailsModal from './EditDetails';
import TextButton from '../../../ui/TextButton';
import { Colors } from '../../../utils/color';

const ChannelProfile = (props) => {
  const {
    avatar,
    title,
    onChannelInfoChange,
  } = props;
  const [showModal, setShowModal] = useState(false);

  return (
    <div className="sendbird-channel-profile">
      <div className="sendbird-channel-profile__avatar">
        <Avatar src={avatar} />
      </div>
      <Label
        type={Typography.SUBTITLE_2}
        color={Colors.ONBACKGROUND_1}
        className="sendbird-channel-profile__title"
      >
        {title}
      </Label>
      <TextButton className="sendbird-channel-profile__edit" onClick={() => setShowModal(true)} notUnderline>
        <Label type={Typography.BUTTON_1} color={Colors.PRIMARY}>
          {StringSet.CHANNEL_SETTING__PROFILE__EDIT}
        </Label>
      </TextButton>
      {
        showModal && (
          <EditDetailsModal
            onCancel={() => setShowModal(false)}
            onSubmit={onChannelInfoChange}
            avatar={avatar}
            title={title}
          />
        )
      }
    </div>
  );
};

ChannelProfile.propTypes = {
  avatar: PropTypes.string,
  title: PropTypes.string,
  onChannelInfoChange: PropTypes.func,
};

ChannelProfile.defaultProps = {
  avatar: '',
  title: '',
  onChannelInfoChange: () => { },
};

export default ChannelProfile;
