import React, { useState, useRef } from 'react';
import PropTypes from 'prop-types';

import './edit-details.scss';
import Modal from '../../../ui/Modal';
import Input, { InputLabel } from '../../../ui/Input';
import Avatar from '../../../ui/Avatar';
import { Type as ButtonType } from '../../../ui/Button/type';
import Label, { Typography, StringSet } from '../../../ui/Label';
import TextButton from '../../../ui/TextButton';
import { Colors } from '../../../utils/color';

const EditDetails = (props) => {
  const {
    onSubmit,
    onCancel,
    avatar,
    title,
  } = props;
  const inputRef = useRef(null);
  const hiddenInputRef = useRef(null);
  const [currentImg, setCurrentImg] = useState(avatar);
  return (
    <Modal
      titleText={StringSet.MODAL__CHANNEL_INFORMATION__TITLE}
      submitText={StringSet.BUTTON__SAVE}
      onCancel={onCancel}
      onSubmit={() => {
        onSubmit(currentImg, inputRef.current.value);
        onCancel();
      }}
      type={ButtonType.PRIMARY}
    >
      <form className="channel-profile-form">
        <div className="channel-profile-form__img-section">
          <InputLabel>
            {StringSet.MODAL__CHANNEL_INFORMATION__CHANNEL_IMAGE}
          </InputLabel>
          <div className="channel-profile-form__avatar">
            <Avatar
              height="80px"
              width="80px"
              src={currentImg}
            />
          </div>
          <input
            ref={hiddenInputRef}
            type="file"
            accept="image/gif, image/jpeg, image/png"
            style={{ display: 'none' }}
            onChange={(e) => {
              setCurrentImg(URL.createObjectURL(e.target.files[0]));
            }}
          />
          <TextButton
            className="channel-profile-form__avatar-button"
            onClick={() => hiddenInputRef.current.click()}
            notUnderline
          >
            <Label type={Typography.BUTTON_1} color={Colors.PRIMARY}>
              {StringSet.MODAL__CHANNEL_INFORMATION__UPLOAD}
            </Label>
          </TextButton>
        </div>
        <div className="channel-profile-form__name-section">
          <InputLabel>
            {StringSet.MODAL__CHANNEL_INFORMATION__CHANNEL_NAME}
          </InputLabel>
          <Input
            ref={inputRef}
            value={title}
            placeHolder={StringSet.MODAL__CHANNEL_INFORMATION__INPUT__PLACE_HOLDER}
          />
        </div>
      </form>
    </Modal>
  );
};

EditDetails.propTypes = {
  onSubmit: PropTypes.func.isRequired,
  onCancel: PropTypes.func.isRequired,
  avatar: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
};

export default EditDetails;
