import React from 'react';
import PropTypes from 'prop-types';

import './index.scss';
import Icon, { Types } from '../Icon';
import Loader from '../Loader';
import Label, { Typography, StringSet } from '../Label';
import PlaceHolderTypes from './type';
import { Colors } from '../../utils/color';

export default function PlaceHolder({
  className,
  type,
  retryToConnect,
}) {
  const injectingClassName = (Array.isArray(className)) ? className : [className];

  return (
    <div className={[...injectingClassName, 'sendbird-place-holder'].join(' ')}>
      {
        type === PlaceHolderTypes.LOADING
          ? (
            <Loader width="48px" height="48px">
              <Icon type={Types.SPINNER} width="48px" height="48px" />
            </Loader>
          )
          : null
      }
      {
        type === PlaceHolderTypes.NO_CHANNELS || type === PlaceHolderTypes.WRONG
          ? (
            <div className="sendbird-place-holder__body">
              <Icon className="sendbird-place-holder__body__icon" type={Types.CHAT} width="64px" height="64px" />
              <Label className="sendbird-place-holder__body__text" type={Typography.BODY_1} color={Colors.ONBACKGROUND_2}>
                {
                  type === PlaceHolderTypes.NO_CHANNELS
                    ? StringSet.PLACE_HOLDER__NO_CHANNEL
                    : StringSet.PLACE_HOLDER__WRONG
                }
              </Label>
              {
                retryToConnect
                  ? (
                    <div
                      className="sendbird-place-holder__body__reconnect"
                      role="button"
                      tabIndex={0}
                      onClick={retryToConnect}
                      onKeyPress={retryToConnect}
                    >
                      <Icon className="sendbird-place-holder__body__reconnect__icon" type={Types.REFRESH} width="20px" height="20px" />
                      <Label className="sendbird-place-holder__body__reconnect__text" type={Typography.BUTTON_1} color={Colors.PRIMARY}>
                        {StringSet.PLACE_HOLDER__RETRY_TO_CONNECT}
                      </Label>
                    </div>
                  )
                  : null
              }
            </div>
          )
          : null
      }
    </div>
  );
}

PlaceHolder.propTypes = {
  className: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.arrayOf(PropTypes.string),
  ]),
  type: PropTypes.oneOfType([
    PropTypes.oneOf(Object.keys(PlaceHolderTypes)),
    PropTypes.string,
  ]).isRequired,
  retryToConnect: PropTypes.func,
};

PlaceHolder.defaultProps = {
  className: '',
  retryToConnect: null,
};
