import React, { useRef } from 'react';
import PropTypes from 'prop-types';

import './index.scss';
import Avatar from '../Avatar';
import IconButton from '../IconButton';
import Icon, { Types } from '../Icon';
import TextButton from '../TextButton';
import Label, { Typography } from '../Label';
import MessageStatus from '../MessageStatus';
import ContextMenu, { MenuItem, MenuItems } from '../ContextMenu';

import { Colors } from '../../utils/color';
import { getSenderProfileUrl, getSenderName, getMessageCreatedAt } from '../../utils/utils';
import { truncate } from './utils';

const MAX_TRUNCATE_LENGTH = 40;
const noop = () => { };

function checkFileType(fileUrl) {
  let result = null;
  const imageFile = /(\.gif|\.jpg|\.jpeg|\.txt|\.pdf)$/i;
  const audioFile = /(\.mp3)$/i;
  if (imageFile.test(fileUrl)) {
    result = Types.FILE_DOCUMENT;
  } else if (audioFile.test(fileUrl)) {
    result = Types.FILE_AUDIO;
  }
  return result;
}

export function OutgoingFileMessage({
  message,
  status,
  showRemove,
}) {
  const {
    url,
  } = message;
  const openFileUrl = () => { window.open(url); };
  const parentRef = useRef(null);
  return (
    <div className="sendbird-file-message__outgoing" ref={parentRef}>
      <ContextMenu
        menuTrigger={(toggleDropdown) => (
          <IconButton
            className="sendbird-file-message__more"
            width="32px"
            height="32px"
            onClick={toggleDropdown}
          >
            <Icon width="22px" height="22px" type={Types.MORE} />
          </IconButton>
        )}
        menuItems={(closeDropdown) => (
          <MenuItems parentRef={parentRef} closeDropdown={closeDropdown}>
            <MenuItem onClick={() => { showRemove(true); closeDropdown(); }}>
              Delete
            </MenuItem>
          </MenuItems>
        )}
      />
      <div className="sendbird-file-message__tooltip">
        {
          checkFileType(url)
            ? (
              <Icon
                className="sendbird-file-message__tooltip__icon"
                width="28px"
                height="28px"
                type={checkFileType(url)}
              />
            )
            : null
        }
        <TextButton
          className="sendbird-file-message__tooltip__text"
          onClick={openFileUrl}
        >
          <Label
            type={Typography.BODY_1}
            color={Colors.ONBACKGROUND_1}
          >
            {truncate(message.url, MAX_TRUNCATE_LENGTH)}
          </Label>
        </TextButton>
      </div>
      <div className="sendbird-file-message__status">
        <MessageStatus message={message} status={status} />
      </div>
    </div>
  );
}

export function IncomingFileMessage({
  message,
}) {
  const openFileUrl = () => { window.open(message.url); };
  return (
    <div className="sendbird-file-message__incoming">
      <Avatar
        className="sendbird-file-message__avatar"
        src={getSenderProfileUrl(message)}
        width="28px"
        height="28px"
      />
      <Label
        className="sendbird-file-message__sender-name"
        type={Typography.CAPTION_2}
        color={Colors.ONBACKGROUND_2}
      >
        {getSenderName(message)}
      </Label>
      <div className="sendbird-file-message__tooltip">
        {
          checkFileType(message.url)
            ? (
              <Icon
                className="sendbird-file-message__tooltip__icon"
                width="28px"
                height="28px"
                type={checkFileType(message.url)}
              />
            )
            : null
        }
        <TextButton
          className="sendbird-file-message__tooltip__text"
          onClick={openFileUrl}
        >
          <Label
            type={Typography.BODY_1}
            color={Colors.ONBACKGROUND_1}
          >
            {truncate(message.url, MAX_TRUNCATE_LENGTH)}
          </Label>
        </TextButton>
      </div>
      <Label
        className="sendbird-file-message__sent-at"
        type={Typography.CAPTION_3}
        color={Colors.ONBACKGROUND_2}
      >
        {getMessageCreatedAt(message)}
      </Label>
    </div>
  );
}

OutgoingFileMessage.propTypes = {
  message: PropTypes.objectOf(PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
    PropTypes.bool,
    PropTypes.array,
    PropTypes.object,
  ])),
  status: PropTypes.string.isRequired,
  showRemove: PropTypes.func.isRequired,
};

OutgoingFileMessage.defaultProps = {
  message: {},
};

IncomingFileMessage.propTypes = {
  message: PropTypes.objectOf(PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
    PropTypes.bool,
    PropTypes.array,
    PropTypes.object,
  ])),
};

IncomingFileMessage.defaultProps = {
  message: {},
};

const MessageSwitch = ({
  message,
  isByMe,
  showRemove,
  status,
}) => (
  <div className={`sendbird-file-message${isByMe ? '--outgoing' : '--incoming'}`}>
    {
      isByMe
        ? (<OutgoingFileMessage message={message} showRemove={showRemove} status={status} />)
        : (<IncomingFileMessage message={message} />)
    }
  </div>
);


MessageSwitch.propTypes = {
  message: PropTypes.shape({}),
  isByMe: PropTypes.bool,
  showRemove: PropTypes.func,
  status: PropTypes.string.isRequired,
};

MessageSwitch.defaultProps = {
  message: {},
  isByMe: false,
  showRemove: noop,
};

export default MessageSwitch;
