import React, {
  useEffect,
  useState,
  useRef,
  useReducer,
} from 'react';
import PropTypes from 'prop-types';

import './index.scss';

import withSendbirdContext from '../../lib/SendbirdSdkContext';
import * as messageActionTypes from './dux/actionTypes';
import messagesInitialState from './dux/initialState';
import messagesReducer from './dux/reducers';
import eventHandler from './dux/eventListener';

import ConversationScroll from './components/ConversationScroll';
import TypingIndicator from './components/TypingIndicator';
import ChatHeader from '../../ui/ChatHeader';
import MessageInput from '../../ui/MessageInput';
import {
  getChannelAvatarSource,
  getChannelTitle,
  getLastSeenAt,
} from '../../ui/ChannelPreview/utils';
import { uuidv4 } from '../../utils/uuid';
import { getParsedStatus, scrollIntoLast } from './utils';
import PlaceHolder from '../../ui/PlaceHolder';
import PlaceHolderTypes from '../../ui/PlaceHolder/type';

const noop = () => {};

export const ConversationPanel = (props) => {
  const {
    channelUrl,
    stores: { sdkStore },
    config: { userId },
    renderChatItem,
    onChatHeaderActionClick,
  } = props;

  const { sdk } = sdkStore;
  const sdkInit = sdkStore.initialized;

  const [messagesStore, messagesDispatcher] = useReducer(messagesReducer, messagesInitialState);
  const [messageSource, setMessageSource] = useState(null);
  const [invalidChannel, setInvalidChannel] = useState(false);
  const [channelHandlerId, setChannelHandlerId] = useState(uuidv4());
  // map to update read status of messages
  const [readStatus, setReadStatus] = useState({});
  const [messageReciverId, setMessageReciverId] = useState();
  const [currentGroupChannel, setCurrentGroupChannel] = useState({});
  const messageInputRef = useRef(null);
  const scrollRef = useRef(null);

  const {
    allMessages,
    loading,
    hasMore,
    initialized,
  } = messagesStore;

  // to create message-datasource
  useEffect(() => {
    messagesDispatcher({
      type: messageActionTypes.RESET_MESSAGES,
    });
    if (!sdkInit) {
      setMessageSource(null);
    }
    if (channelUrl && sdkInit) {
      messagesDispatcher({
        type: messageActionTypes.SET_CURRENT_CHANNEL,
        payload: channelUrl,
      });
      // remove previous handler
      sdk.removeChannelHandler(channelHandlerId);
      sdk.GroupChannel.getChannel(channelUrl, (groupChannel) => {
        if (!groupChannel) {
          setInvalidChannel(true);
          return;
        }

        setInvalidChannel(false);
        setCurrentGroupChannel(groupChannel);

        // for handling read status on the go
        const handler = new sdk.ChannelHandler();
        handler.onReadReceiptUpdated = (c) => {
          if (channelUrl === c.url) {
            const allReadStatus = allMessages.reduce((accumulator, msg) => {
              if (msg.messageId !== 0) {
                return {
                  ...accumulator,
                  [msg.messageId]: getParsedStatus(msg, groupChannel),
                };
              }
              return accumulator;
            }, {});
            setReadStatus(allReadStatus);
          }
        };

        const newHandlerId = uuidv4();
        sdk.addChannelHandler(newHandlerId, handler);
        setChannelHandlerId(newHandlerId);

        // this order is important - this mark as read should update the event handler up above
        groupChannel.markAsRead();

        // There should only be one single instance per channel view.
        const prevMessageListQuery = groupChannel.createPreviousMessageListQuery();
        prevMessageListQuery.limit = 30;
        prevMessageListQuery.reverse = false;

        setMessageSource(prevMessageListQuery);

        messagesDispatcher({
          type: messageActionTypes.GET_PREV_MESSAGES_START,
        });

        // Retrieving previous messages.
        prevMessageListQuery.load((messages, error) => {
          if (error) {
            // maybe - add error state
            return;
          }
          messagesDispatcher({
            type: messageActionTypes.GET_PREV_MESSAGES_SUCESS,
            payload: {
              messages,
              hasMore: prevMessageListQuery.hasMore,
            },
          });
          setTimeout(() => scrollIntoLast('.sendbird-msg-hoc'));
        });
      });
    }
  }, [channelUrl, sdkInit]);

  // todo: cleanup internal state - this is for removing messages from other person
  useEffect(() => {
    setMessageReciverId(uuidv4());
    if (channelUrl && sdk) {
      eventHandler({ messagesDispatcher, sdk });
    }
    return () => {
      if (sdk) {
        sdk.removeChannelHandler(messageReciverId);
      }
    };
  }, [channelUrl, sdkInit]);

  // to create initial read status
  useEffect(() => {
    if (allMessages.length > 0) {
      const allReadStatus = allMessages.reduce((accumulator, msg) => {
        if (msg.messageId !== 0) {
          return {
            ...accumulator,
            [msg.messageId]: getParsedStatus(msg, currentGroupChannel),
          };
        }
        return accumulator;
      }, {});
      setReadStatus(allReadStatus);
    }
  }, [allMessages]);

  if (loading) {
    return (<PlaceHolder type={PlaceHolderTypes.LOADING} />);
  }

  if (invalidChannel) {
    return (<PlaceHolder type={PlaceHolderTypes.WRONG} />);
  }
  return (
    <div className="sendbird-conversation">
      <ChatHeader
        onActionClick={onChatHeaderActionClick}
        avatar={getChannelAvatarSource(currentGroupChannel)}
        title={getChannelTitle(currentGroupChannel)}
        getLastSeenAt={() => getLastSeenAt(currentGroupChannel)}
        isActive={false}
        isMuted={false}
      />
      <ConversationScroll
        scrollRef={scrollRef}
        hasMore={hasMore}
        messagesDispatcher={messagesDispatcher}
        messageSource={messageSource}
        initialized={initialized}
        allMessages={allMessages}
        userId={userId}
        readStatus={readStatus}
        currentGroupChannel={currentGroupChannel}
        renderChatItem={renderChatItem}
        deleteMessage={(message, cb) => {
          currentGroupChannel.deleteMessage(message, (err) => {
            if (cb) {
              cb(err);
            }
            if (!err) {
              messagesDispatcher({
                type: messageActionTypes.ON_MESSAGE_DELETED,
                payload: message.messageId,
              });
            }
          });
        }}
        updateMessage={(messageId, text, cb) => {
          const params = new sdk.UserMessageParams();
          params.message = text;
          currentGroupChannel.updateUserMessage(messageId, params, (message, err) => {
            if (cb) {
              cb(err, message);
            }
            if (!err) {
              messagesDispatcher({
                type: messageActionTypes.ON_MESSAGE_UPDATED,
                payload: message,
              });
            }
          });
        }}
      />
      <div className="sendbird-conversation__footer">
        <MessageInput
          ref={messageInputRef}
          disabled={!initialized}
          onStartTyping={() => {
            currentGroupChannel.startTyping();
          }}
          onSendMessage={() => {
            const text = messageInputRef.current.value;
            const params = new sdk.UserMessageParams();
            params.message = text;
            const pendingMsg = currentGroupChannel.sendUserMessage(params, (message, error) => {
              if (error) {
                messagesDispatcher({
                  type: messageActionTypes.SEND_MESSAGEGE_FAILURE,
                  payload: params,
                });
                return;
              }
              messagesDispatcher({
                type: messageActionTypes.SEND_MESSAGEGE_SUCESS,
                payload: message,
              });
            });
            messagesDispatcher({
              type: messageActionTypes.SEND_MESSAGEGE_START,
              payload: pendingMsg,
            });
            setTimeout(() => scrollIntoLast('.sendbird-msg-hoc'));
          }}
          onFileUpload={(file) => {
            const params = new sdk.FileMessageParams();
            params.file = file;
            const pendingMsg = currentGroupChannel.sendFileMessage(params, (message, error) => {
              if (error) {
                messagesDispatcher({
                  type: messageActionTypes.SEND_MESSAGEGE_FAILURE,
                  payload: params,
                });
                return;
              }
              messagesDispatcher({
                type: messageActionTypes.SEND_MESSAGEGE_SUCESS,
                payload: message,
              });
            });
            messagesDispatcher({
              type: messageActionTypes.SEND_MESSAGEGE_START,
              payload: pendingMsg,
            });
            setTimeout(() => scrollIntoLast('.sendbird-msg-hoc'));
          }}
        />
        <div className="sendbird-conversation__typing-indicator">
          <TypingIndicator channelUrl={channelUrl} sb={sdk} />
        </div>
      </div>
    </div>
  );
};

ConversationPanel.propTypes = {
  channelUrl: PropTypes.string.isRequired,
  stores: PropTypes.shape({
    sdkStore: {
      initialized: PropTypes.bool,
    },
  }).isRequired,
  dispatchers: PropTypes.shape({
    messagesDispatcher: PropTypes.func.isRequired,
    channelsDispatcher: PropTypes.func.isRequired,
  }).isRequired,
  config: PropTypes.shape({
    userId: PropTypes.string.isRequired,
  }).isRequired,
  renderChatItem: PropTypes.element,
  onChatHeaderActionClick: PropTypes.func,
};

ConversationPanel.defaultProps = {
  renderChatItem: null,
  onChatHeaderActionClick: noop,
};

export default withSendbirdContext(ConversationPanel);
