import React from 'react';
import PropTypes from 'prop-types';

import './index.scss';
import Avatar from '../Avatar';
import Label, { Typography, Colors } from '../Label';
import Icon, { Types } from '../Icon';
import IconButton from '../IconButton';
import AutoRefresh from '../AutoRefresh';

export default function ChatHeader({
  avatar,
  title,
  subTitle,
  getLastSeenAt,
  isActive,
  isMuted,
  onActionClick,
}) {
  return (
    <div className="sendbird-chat-header">
      <div className="sendbird-chat-header__left">
        <Avatar
          className="sendbird-chat-header__avatar"
          src={avatar}
          width="32px"
          height="32px"
        />
        <Label
          className="sendbird-chat-header__title"
          type={Typography.H_2}
          color={Colors.ONBACKGROUND_1}
        >
          {title}
        </Label>
        {
          (typeof isActive === 'string' && isActive === 'true') || (typeof isActive === 'boolean' && isActive)
            ? <div className="sendbird-chat-header__active" />
            : null
        }
        <Label
          className="sendbird-chat-header__subtitle"
          type={Typography.BODY_1}
          color={Colors.ONBACKGROUND_2}
        >
          {subTitle || <AutoRefresh repeatFunc={getLastSeenAt} />}
        </Label>
      </div>
      <div className="sendbird-chat-header__right">
        {
          (typeof isMuted === 'string' && isMuted === 'true') || (typeof isMuted === 'boolean' && isMuted)
            ? <Icon className="sendbird-chat-header__mute" type={Types.MUTE} width="24px" height="24px" />
            : null
        }
        <IconButton
          className="sendbird-chat-header__info"
          width="32px"
          height="32px"
          onClick={onActionClick}
        >
          <Icon type={Types.INFO} width="22px" height="22px" />
        </IconButton>
      </div>
    </div>
  );
}

ChatHeader.propTypes = {
  avatar: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.arrayOf(PropTypes.string),
  ]),
  title: PropTypes.string,
  getLastSeenAt: PropTypes.func,
  subTitle: PropTypes.string,
  isActive: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.bool,
  ]),
  isMuted: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.bool,
  ]),
  onActionClick: PropTypes.func,
};

ChatHeader.defaultProps = {
  avatar: '',
  title: '',
  subTitle: '',
  getLastSeenAt: () => { },
  isActive: false,
  isMuted: false,
  onActionClick: () => { },
};
