import React, { Component, useState } from 'react';
import { createPortal } from 'react-dom';
import PropTypes from 'prop-types';

import './index.scss';

import Label from '../Label';
import { Typography, Colors } from '../Label/types';

export const MenuItem = ({ children, onClick }) => (
  <li
    tabIndex={0}
    className="sendbird-dropdown__menu-item"
    onClick={onClick}
    onKeyPress={(e) => {
      if (e.keyCode === 13) {
        onClick(e);
      }
    }}
    role="menuitem"
  >
    <Label
      type={Typography.SUBTITLE_2}
      color={Colors.ONBACKGROUND_1}
      className="sendbird-dropdown__menu-item__text"
    >
      {children}
    </Label>
  </li>
);

MenuItem.propTypes = {
  onClick: PropTypes.func.isRequired,
  children: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.element,
  ]).isRequired,
};


export const MenuRoot = () => (
  <div id="sendbird-dropdown-portal" />
);

export class MenuItems extends Component {
  constructor(props) {
    super(props);
    this.menuRef = React.createRef();
    this.state = {
      menuStyle: {},
      handleClickOutside: () => {},
    };
  }

  componentDidMount() {
    this.setupEvents();
    this.getMenuPosition();
  }

  componentWillUnmount() {
    this.cleanUpEvents();
  }

  setupEvents = () => {
    const { closeDropdown } = this.props;
    const { menuRef } = this;
    const handleClickOutside = (event) => {
      if (menuRef.current && !menuRef.current.contains(event.target)) {
        closeDropdown();
      }
    };
    this.setState({
      handleClickOutside,
    });

    document.addEventListener('mousedown', handleClickOutside);
  }

  cleanUpEvents = () => {
    const {
      handleClickOutside,
    } = this.state;
    document.removeEventListener('mousedown', handleClickOutside);
  }

  getMenuPosition = () => {
    const { parentRef } = this.props;
    const parentRect = parentRef.current.getBoundingClientRect();
    const { x, y } = parentRect;
    const menuStyle = {
      top: y,
      left: x,
    };

    if (!this.menuRef.current) return menuStyle;

    const { innerWidth, innerHeight } = window;
    const rect = this.menuRef.current.getBoundingClientRect();

    if (y + rect.height > innerHeight) {
      menuStyle.top -= rect.height;
    }

    if (x + rect.width > innerWidth) {
      menuStyle.left -= rect.width;
    }

    if (menuStyle.top < 0) {
      menuStyle.top = rect.height < innerHeight ? (innerHeight - rect.height) / 2 : 0;
    }

    if (menuStyle.left < 0) {
      menuStyle.left = rect.width < innerWidth ? (innerWidth - rect.width) / 2 : 0;
    }
    return this.setState({ menuStyle });
  }

  render() {
    const { menuStyle } = this.state;
    const { children } = this.props;
    return (
      createPortal(
        (
          <>
            <div className="sendbird-dropdown__menu-backdrop" />
            <ul
              className="sendbird-dropdown__menu"
              ref={this.menuRef}
              style={{
                display: 'inline-block',
                position: 'fixed',
                left: `${Math.round(menuStyle.left)}.px`,
                top: `${Math.round(menuStyle.top)}.px`,
              }}
            >
              { children }
            </ul>
          </>
        ),
        document.getElementById('sendbird-dropdown-portal'),
      )
    );
  }
}
MenuItems.propTypes = {
  closeDropdown: PropTypes.func.isRequired,
  children: PropTypes.oneOfType([
    PropTypes.element,
    PropTypes.arrayOf(PropTypes.element),
  ]).isRequired,
  // https://stackoverflow.com/a/51127130
  parentRef: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({ current: PropTypes.instanceOf(Element) }),
  ]).isRequired,
};

export default function ContextMenu(props) {
  const [showMenu, setShowMenu] = useState(false);
  const { menuTrigger, menuItems } = props;

  return (
    <div style={{ display: 'inline' }}>
      { menuTrigger(() => setShowMenu(!showMenu)) }
      { showMenu && menuItems(() => setShowMenu(false)) }
    </div>
  );
}

ContextMenu.propTypes = {
  menuTrigger: PropTypes.func.isRequired,
  menuItems: PropTypes.func.isRequired,
};
